import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'

import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Catalog from '../../components/Catalog'

import './styles.css'

export default class Category extends PureComponent {
  render () {
    return (
      <div className="page">
        <Header />

        <main>
          <div className="breadcrumbs">
            <ul>
              <li><Link to="/">Главная</Link></li>
              <li>Выставочные системы</li>
              <li><Link to="/catalog/stands">Мобильные стенды</Link></li>
            </ul>
          </div>

          <section className="region region--odd">
            <div className="category">
              <h2 className="category__header">
                Мобильные стенды
              </h2>

              <Catalog
                category="Мобильные стенды"
                mode="short"
              />

              <div className="category__content">
                <div className="category__highlight">
                  <div className="category__highlight_header">
                    Преимущества
                  </div>
                  <div className="category__highlight_text">
                    <ul>
                      <li>экономия средств за счет многоразового использования;</li>
                      <li>легкая замена баннеров;</li>
                      <li>удобная транспортировка за счет мобильности и небольшого веса конструкций.</li>
                    </ul>
                  </div>
                </div>
                <div className="category__text">
                  <div className="category__lead">
                    <p>Используется для оформления рекламного пространства в помещениях.</p>
                    <p>Х стенд (паук) — мобильный баннерный стенд в виде Х - образной стойки с четырехточечным креплением постера на люверсах. В качестве рекламного носителя для X стенда рекомендуется использовать баннер из легких материалов, т.к. тяжелая виниловая ткань может его повредить.</p>
                  </div>
                  <div className="category__info">
                    <p>Рекомендуемый материал баннера: ламинированная бумага, полиэстеровая ткань, полипропилен, тонкий пластик (0,3-0,5 мм).</p>
                    <p>Крепление баннера — с помощью люверсов.</p>
                    <p>Поставляется в чехле.</p>
                    <p>Не требует приобретения дополнительных аксессуаров.</p>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>

        <Footer />
      </div>
    )
  }
}



