import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'

import './styles.css'

export default class Footer extends PureComponent {
  render () {
    return (
      <footer className="footer">
        <div><Link to="/catalog/short">Мобильные стенды (оптимальный выбор)</Link></div>
        <div><Link to="/catalog/full">Мобильные стенды (все)</Link></div>
        <div><Link to="/contacts">Контакты</Link></div>
        <div><Link to="/pr/stands">Прототип: выставочные стенды</Link></div>
        <div><Link to="/pr/standsX">Прототип: Стенды на люверсах</Link></div>
      </footer>
    )
  }
}

Footer.propTypes = {}



