import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'

import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Catalog from '../../components/Catalog'

import './styles.css'

export default class Category extends PureComponent {
  render () {
    return (
      <div className="page">
        <Header />

        <main>
          <section className="stands01">
            <h2 className="stands01__header">Стенды на люверсах</h2>
            <div className="stands01__subheader">X-стенды, "паук", Y-стенды</div>

            <div className="stands01__lead">
              <p>Пример текста... Используется для оформления рекламного пространства в помещениях. Для размещения на улице такие стенды не подходят, поскольку их конструкция, изготовленная из легких материалов, не предполагает использование утяжелителей.</p>
            </div>

            <div className="stands01__catalog">
              <div className="stands01__sidebar">
                <div className="stands01_subcats">
                  <div className="stands01_subcats__header">
                    <p>Другие типы стендов</p>
                  </div>
                  <div className="stands01_subcats__lead">
                    <p>Пример текста... Выставочные стенды делятся на несколько типов по типу крепления рекламного поля.</p>
                  </div>

                  <div className="stands01_subcats__list">
                    <div className="item active">
                      <div className="title">На люверсах</div>
                      <div className="desc">X-стенды, Y-стенды и подобные</div>
                    </div>

                    <div className="item">
                      <div className="title">На профиле</div>
                      <div className="desc">L-стенды, H-стенды и их модификации</div>
                    </div>

                    <div className="item">
                      <div className="title">Ролл-стенды</div>
                      <div className="desc">Статические и динамические ролл-стенды</div>
                    </div>

                    <div className="item">
                      <div className="title">Мобильные прессволы</div>
                      <div className="desc">Стенды увеличенного размера под мягкие банныеры и фотопанели</div>
                    </div>

                    <div className="item">
                      <div className="title">Аксессуары</div>
                      <div className="desc">Аксессуары ков всем видам выставочных стендов</div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="stands01__content">
                <div className="stands01_filter quick">
                  <div className="title">
                    Быстрые фильтры
                  </div>
                  <div className="desc">Пример текста.... Мы собрали для вас полезные наборы стендов для облегчения выбора среди 15 различных стендов:</div>
                  <ul className="list">
                    <li>двухсторонний</li>
                    <li>уличное использование</li>
                    <li>настольный</li>
                    <li>изменяемый размер</li>
                    <li>увеличенного размера</li>
                    <li>из бамбука</li>
                    <li>повышенной устойчивости</li>
                  </ul>
                </div>

                <div className="stands01_filter">
                  <div className="title">Размер рекламного поля</div>
                  <div className="desc">Пример текста... Основной характеристикой для выставочных стендов является размер рекламаного поля. Вы можете подобрать подходящий размер или просмотреть все стенды.</div>
                  <pre style={{width: "600px", margin: "0 auto"}}>----------0--------------||-------------0---------<br/>         100                           200</pre>
                </div>

                <div className="stands01_filter">
                  <div className="title">Дополнительные услуги</div>
                  <div className="desc">Пример текста... Вы можете сравнить цены на все стенды с учетом дополнительных услуг:</div>
                    <ul className="list">
                      <li>&#10004; печать баннера</li>
                      <li>&#10004; дизайн баннера</li>
                    </ul>
                </div>

                <div className="stands01_products">
                  <ul className="list">
                    <li>JusT Up Light 100x200 см</li>
                    <li>JusT Up -х2 Light</li>
                    <li>JusT "L" FLex, 100х200 см</li>
                    <li>JUST ROLL ТИП С 100 см</li>
                    <li>JUST ROLL - Х2 100 см</li>
                    <li>JUST BIG LUX 100 см</li>
                  </ul>
                </div>
              </div>
            </div>
          </section>
        </main>

        <Footer />
      </div>
    )
  }
}



