import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch
} from 'react-router-dom'

import ScrollToTop from './components/ScrollToTop'
import Home from './scenes/Home'
import Category from './scenes/Category'
import Category0 from './scenes/Category0'
import Stands01 from './scenes/Stands01'
import Stands02 from './scenes/Stands02'
import Contacts from './scenes/Contacts'
import NotFound from './scenes/NotFound'

class App extends Component {
  render() {
    return (
      <Router
        basename="/presswall"
      >
        <ScrollToTop>
          <div>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/contacts" component={Contacts} />
              <Route path="/catalog/full" component={Category} />
              <Route path="/catalog/short" component={Category0} />
              <Route path="/pr/stands" component={Stands01} />
              <Route path="/pr/standsX" component={Stands02} />
              <Route component={NotFound} />
            </Switch>
          </div>
        </ScrollToTop>
      </Router>
    )
  }
}

export default App;



